




































.categories-cont {
  display: grid;
  grid-gap: 14px;
  grid-auto-rows: minmax(100px, auto);
  grid-template-columns: repeat(auto-fill, minmax(90px, 1fr));

  img {
    filter: invert(0.65);
    width: 70%;
  }
}

.mark-fav-btn {
  top: -9px;
  right: -8px;
  padding: 3px 4px 4px 4px;
  width: 25px;
  height: 25px;
  border-color: #ff545e;
}
